.is-active {
  visibility: visible;
  pointer-events: all;
  opacity: 1;
  transition: all .25s ease-in-out .25s;
  transform: translate(-50%, -50%)scale(1)rotate(0deg);
}

.cat-info {
  min-width: 700px;
  border-radius: 4px;
  display: block;
}

.img-cat {
  border-radius: 15%;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: 0 0 8px rgba(241, 245, 162, .47);
}

